* {
    box-sizing: border-box;
  }
  
  body {
    margin: 0px;
    font-family: Georgia, 'Times New Roman', Times, serif;
  }
  
  .nav {
    background-image: linear-gradient(320deg, #427387 0, #40707f 6.25%, #406c77 12.5%, #40666d 18.75%, #416063 25%, #41595a 31.25%, #415150 37.5%, #414847 43.75%, #3f3f3f 50%, #3c3638 56.25%, #392e33 62.5%, #352630 68.75%, #30202d 75%, #2b1b2c 81.25%, #25172b 87.5%, #1f142b 93.75%, #18122c 100%);
    height: 50px;
    width: 100%;
    position: relative;
    opacity: 0.8;
    
  }
  
  .nav > .nav-header {
    display: inline;
    
  }

  
  .nav > .nav-header > .nav-title {
    -webkit-text-stroke: 1px rgb(218, 216, 216); 
    display: inline-block;
    font-size: 30px;
    letter-spacing: 5px;
    margin-top: 0.5%;
    margin-left: -25rem;
    font-family: Georgia, 'Times New Roman', Times, serif;
  }
  
  .nav > .nav-btn {
    display: none;
  }
  
  .nav > .nav-links {
    display: inline;
    float: right;
    font-size: 18px;
  }
  
  .nav > .nav-links > a {
    display: inline-block;
    padding: 13px 10px 13px 10px;
    text-decoration: none;
    color: #efefef;
    font-family: Georgia, 'Times New Roman', Times, serif;  
  }
  
  .nav > .nav-links > a:hover {
    background-color: rgba(0, 0, 0, 0.1);
  }
  
  .nav > #nav-check {
    display: none;
  }
  
  @media (max-width:600px) {
    .nav > .nav-btn {
      display: inline-block;
      position: absolute;
      right: 0px;
      top: 0px;
    }
    .nav > .nav-btn > label {
      display: inline-block;
      width: 50px;
      height: 50px;
      padding: 13px;
    }
    .nav > .nav-btn > label:hover,.nav  #nav-check:checked ~ .nav-btn > label {
      background-color: rgba(0, 0, 0);
    }
    .nav > .nav-btn > label > span {
      display: block;
      width: 25px;
      height: 10px;
      border-top: 2px solid #eee;
    }
    .nav > .nav-links {
      position: absolute;
      display: block;
      width: 100%;
      background-color: #333;
      height: 0px;
      transition: all 0.3s ease-in;
      overflow-y: hidden;
      top: 50px;
      left: 0px;
    }
    .nav > .nav-links > a {
      display: block;
      width: 100%;
    }
    .nav > #nav-check:not(:checked) ~ .nav-links {
      height: 0px;
    }
    .nav > #nav-check:checked ~ .nav-links {
      height: calc(100vh - 50px);
      overflow-y: auto;
    }
  }
